body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.sidebarStyle {
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
  margin: 12px;
  background-color: #104040;
  color: #ffffff;
  z-index: 1 !important;
  padding: 12px;
  font-weight: bold;
  border-radius: 25px;
}


.mapContainer {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  left: 0%;
  bottom: 0;
}


.marker {
  background-image: url('./icon.png');
  background-size: cover;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  cursor: pointer;
}

.mapboxgl-popup {
  max-width: 70vh;
  max-height: 100vh;

}

.mapboxgl-popup-content {
  max-width: 60vh;
  max-height: 80vh;
  text-align: left;
  font-family: 'Open Sans', sans-serif;
  font-size: 1vw;
  white-space: normal;

  overflow-y: scroll;
  border-radius: 25px;
  padding: 15px;
  line-height: 1.3vh;
  background: lightblue;

}


.button {
  margin-top: 20px;
  max-width: 200px;
  line-height: 35px;

  padding: 10px;
  background: CadetBlue;
  border: none;
  font-size: 2vw;
  white-space: normal;
  overflow: hidden;
  text-overflow: ellipsis;
  border-radius: 25px;
}

.button:hover {
  background: AliceBlue;
}
.show_more {
  margin-top: 20px;
  max-width: 200px;
  line-height: 35px;

  padding: 10px;
  background: CadetBlue;
  border: none;
  font-size: 2vw;
  white-space: normal;
  overflow: hidden;
  text-overflow: ellipsis;
  border-radius: 25px;
}

.show_more:hover {
  background: AliceBlue;
}

@media only screen and (max-width: 480px) {
    .mapboxgl-popup-content {
        max-width: 250px;
    }
    .mapboxgl-popup-content div {
        padding:0em;
    }
    .mapboxgl-popup-content p {
        margin: 0.5em 0.5em;
    }
}
